@font-face {
    font-family: 'orbitron';
    src: url('/assets/fonts/orbitron-bold-webfont.woff2') format('woff2'),
         url('/assets/fonts/orbitron-bold-webfont.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'source_sans_pro';
    src: url('/assets/fonts/sourcesanspro-regular-webfont.woff2') format('woff2'),
         url('/assets/fonts/sourcesanspro-regular-webfont.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'teko_numbers';
    src: url('/assets/fonts/teko-regular-webfont.woff2') format('woff2'),
         url('/assets/fonts/teko-regular-webfont.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}

html, body {
	height: 100%;
	width: 100%;
	padding: 0;
	margin: 0;
	background: #2B2B5E;
	font-family: 'Helvetica Neue', arial, sans-serif;
	font-weight: 400;
	color: #fff;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	font-family: source_sans_pro, sans-serif;
}

a, a:link {
  color: #4598FF;
}
a:hover, a:active {
  color: #73B1FF;
}

*, *:before, *:after {
	box-sizing: border-box;
}

#app {
	border: 10px solid #2B2B5E;
  box-shadow: inset 0px 0px 100px rgba(25, 24, 49, 0.1);
  background: #212146 url('/assets/images/page-bg.jpg') top center no-repeat;
  background-size: cover;
  background-attachment: fixed;
  padding-top: 20px;
  min-width: 280px;
}

@media screen and (min-width: 351px) {
  #app {
    border-width: 20px;
  }
}

@media screen and (min-width: 768px) {
  #app {
    background-attachment: scroll;
  }
  .title h1 {
    font-size: 35px;
  }
}



